@tailwind base;
@tailwind components;
@tailwind utilities;

html, 
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  overflow-y: auto;
  width: 100%;
}

/* Reducir el espacio entre el header y el contenido principal */
main {
  padding-top: 60px !important; /* Esto sobrescribirá el padding-top en el componente */
}

/* Animación para el menú móvil */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-slideDown {
  animation: slideDown 0.3s ease forwards;
}

/* Asegurarse que el contenido principal tenga suficiente espacio */
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
:root {
  --color-green-primary: #15803d;  /* green-800 */
  --color-green-secondary: #166534; /* green-900 */
  --color-amber-primary: #d97706;  /* amber-600 */
  --color-amber-secondary: #92400e; /* amber-800 */
  --color-yellow-accent: #facc15;  /* yellow-400 */
  --header-height: 64px;
  --footer-height: 80px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

#root {
  height: 100%;
}

/* Estilos responsivos generales */
.container {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

/* Layout y estructura de página */
.flex-col {
  display: flex;
  flex-direction: column;
}

.min-h-screen {
  min-height: 100vh;
}

/* Header adaptable */
header {
  z-index: 50;
  position: sticky;
  top: 0;
  transition: all 0.3s ease;
}

.mobile-menu {
  position: fixed;
  top: var(--header-height);
  left: 0;
  right: 0;
  background-color: var(--color-green-primary);
  transform: translateY(-100%);
  transition: transform 0.3s ease-in-out;
  z-index: 40;
}

.mobile-menu.open {
  transform: translateY(0);
}

/* Animaciones */
@keyframes slideDown {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.animate-slideDown {
  animation: slideDown 0.3s ease forwards;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-fadeIn {
  animation: fadeIn 0.8s ease-out forwards;
}

.animate-fadeInUp {
  animation: fadeInUp 0.8s ease-out forwards;
}

.animate-fadeInLeft {
  animation: fadeInLeft 0.8s ease-out forwards;
}

.animate-fadeInRight {
  animation: fadeInRight 0.8s ease-out forwards;
}

.animate-zoomIn {
  animation: zoomIn 0.5s ease-out forwards;
}

.animate-spin-reverse {
  animation: spin 1.5s linear infinite reverse;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes beeLoading {
  0% {
    transform: translateY(0) scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: translateY(-10px) scale(1);
    opacity: 1;
  }
}

@keyframes float {
  0% { transform: translate(-50%, -50%) translateY(0px) rotate(0deg); }
  25% { transform: translate(-50%, -50%) translateY(-15px) rotate(3deg); }
  50% { transform: translate(-50%, -50%) translateY(0px) rotate(0deg); }
  75% { transform: translate(-50%, -50%) translateY(15px) rotate(-3deg); }
  100% { transform: translate(-50%, -50%) translateY(0px) rotate(0deg); }
}

.bee-float {
  animation: float 6s ease-in-out infinite;
}

/* Adaptación móvil para el Hero */
.hero-section {
  background-size: cover !important;
  background-position: center center !important;
}

@media (max-width: 768px) {
  .hero-section {
    height: 50vh !important;
  }
  
  .hero-button {
    right: 50% !important;
    transform: translateX(50%) !important;
    top: 70% !important;
    z-index: 10 !important;
  }
}

/* Estilos para el BeeAtlas Component */
.atlas-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.atlas-content {
  flex-grow: 1;
  overflow: hidden;
  position: relative;
}

.page-navigation {
  position: absolute;
  bottom: 1rem;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  z-index: 30;
}

@media (max-width: 640px) {
  .atlas-content {
    height: calc(100vh - var(--header-height) - 60px);
  }
  
  .navigation-button {
    width: 40px !important;
    height: 40px !important;
  }
}

/* Animaciones de transición de página */
.page-exit-to-left {
  transform: translateX(-100%);
  opacity: 0;
}

.page-exit-to-right {
  transform: translateX(100%);
  opacity: 0;
}

.page-enter-from-right {
  transform: translateX(0);
  opacity: 1;
}

.page-enter-from-left {
  transform: translateX(0);
  opacity: 1;
}

/* PageBase Component responsivo */
.page-base-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

@media (min-width: 768px) {
  .page-base-container {
    flex-direction: row;
  }
  
  .page-side-panel {
    width: 40%;
    height: 100%;
  }
  
  .page-content-panel {
    width: 60%;
    height: 100%;
  }
}

@media (max-width: 767px) {
  .page-side-panel {
    height: 30vh;
  }
  
  .page-content-panel {
    height: 70vh;
  }
}

/* Mejoras para la tabla de contenidos */
.toc-container {
  width: 100%;
  overflow-y: auto;
  padding-bottom: 2rem;
}

@media (max-width: 768px) {
  .toc-bee-image {
    display: none;
  }
  
  .toc-grid {
    grid-template-columns: 1fr !important;
  }
}

/* Ajustes específicos para el botón de panal de abeja */
.honeycomb-button {
  transition: all 0.3s ease;
  overflow: hidden;
}

.honeycomb-button:hover {
  transform: translateY(-5px) scale(1.05);
}

.honeycomb-pattern {
  background-color: transparent;
  background-image: radial-gradient(circle at center, rgba(0,0,0,0.1) 2px, transparent 2px);
  background-size: 12px 12px;
  width: 50%;
  height: 50%;
  animation: floatHoneycomb 8s infinite linear;
}

@keyframes floatHoneycomb {
  0% { background-position: 0 0; }
  100% { background-position: 12px 12px; }
}

/* Estilos para el hexágono (botón de panal) */
.honeycomb-shape {
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}

/* Estilos para las tarjetas de contenido */
.content-card {
  transform: translateY(0);
  transition: transform 0.3s ease;
}

.content-card:hover {
  transform: translateY(-10px);
}

/* Media queries específicas */
@media (max-width: 480px) {
  .hero-button {
    width: 100px !important;
    height: 100px !important;
  }
  
  .section-title {
    font-size: 1.5rem !important;
  }
  
  .navigation-dots {
    display: none;
  }
}

@media (max-width: 768px) {
  .content-grid {
    grid-template-columns: 1fr !important;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}

/* Soporte para pantallas muy grandes */
@media (min-width: 1536px) {
  .container {
    max-width: 1536px;
  }
  
  .hero-section {
    background-size: cover !important;
  }
}

/* Utilidades para scroll */
.scroll-smooth {
  scroll-behavior: smooth;
}

.overflow-y-auto {
  overflow-y: auto;
}

.overflow-x-hidden {
  overflow-x: hidden;
}

/* Fix para Safari Mobile y el 100vh */
@supports (-webkit-touch-callout: none) {
  .h-screen {
    height: -webkit-fill-available;
  }
  
  .min-h-screen {
    min-height: -webkit-fill-available;
  }
}

/* Estilos específicos para la pantalla de carga */
.loading-container {
  position: fixed;
  inset: 0;
  background-color: rgba(251, 191, 36, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.loading-spinner {
  position: relative;
  width: 80px;
  height: 80px;
}

.loading-circle {
  position: absolute;
  border-radius: 50%;
  border: 4px solid;
  animation: spin 1.5s linear infinite;
}

.loading-circle-outer {
  inset: 0;
  border-color: var(--color-amber-secondary);
  border-top-color: transparent;
}

.loading-circle-middle {
  inset: 10px;
  border-color: var(--color-amber-primary);
  border-right-color: transparent;
  border-top-color: transparent;
  animation-direction: reverse;
}

.loading-circle-inner {
  inset: 20px;
  border-color: var(--color-yellow-accent);
  border-bottom-color: transparent;
  border-top-color: transparent;
}

.loading-bee {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  animation: pulse 1.5s ease infinite alternate;
}

@keyframes pulse {
  from { opacity: 0.7; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1.1); }
}

/* Ajuste específico para footer responsivo */
footer {
  padding-bottom: env(safe-area-inset-bottom);
}

.footer-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .footer-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}